import React, { Component } from "react";
import { Link, withRouter } from "react-router-dom";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { registerUser } from "../../actions/authActions";
import classnames from "classnames";
import Navbar from '../Front/navbar.component.js';

class Register extends Component {
  constructor() {
    super();
    this.state = {
      name: "",
      email: "",
      password: "",
      password2: "",
      errors: {}
    };
  }

  componentDidMount() {
    // If logged in and user navigates to Register page, should redirect them to dashboard
    if (this.props.auth.isAuthenticated) {
      this.props.history.push("/dashboard");
    }
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({
        errors: nextProps.errors
      });
    }
  }

  onChange = e => {
    this.setState({ [e.target.id]: e.target.value });
  };

  onSubmit = e => {
    e.preventDefault();

    const newUser = {
      name: this.state.name,
      email: this.state.email,
      password: this.state.password,
      password2: this.state.password2
    };

    this.props.registerUser(newUser, this.props.history);
  };

  render() {
    const { errors } = this.state;

    return (
      <>
      <Navbar />
      <div className="col-12" style={{backgroundColor: "#89cff0", height:"100vh"}}>
        <div style={{ paddingTop: "4rem" }} className="row">
          <div className="col-md-6 m-auto pt-4" style={{backgroundColor: "#fff", boxShadow:"0px 0px 5px #000"}}>

            <div className="col-12 text-center">
              <h4>
                <b>Register</b> below
              </h4>

            </div>
            <form noValidate className="py-4" onSubmit={this.onSubmit}>
              <div className="input-field col-10 mx-auto">
                <label htmlFor="name">Name</label>
                <br />
                <input
                  onChange={this.onChange}
                  value={this.state.name}
                  error={errors.name}
                  id="name"
                  type="text"
                  className={classnames("col-12", {
                    invalid: errors.name
                  })}
                />

                <span className="red-text">{errors.name}</span>
              </div>
              <div className="input-field col-10 mx-auto">
                <label htmlFor="email">Email</label>
                <br />
                <input
                  onChange={this.onChange}
                  value={this.state.email}
                  error={errors.email}
                  id="email"
                  type="email"
                  className={classnames("col-12", {
                    invalid: errors.email
                  })}
                />

                <span className="red-text">{errors.email}</span>
              </div>
              <div className="input-field col-10 mx-auto">
                <label htmlFor="password">Password</label>
                <br />
                <input
                  onChange={this.onChange}
                  value={this.state.password}
                  error={errors.password}
                  id="password"
                  type="password"
                  className={classnames("col-12", {
                    invalid: errors.password
                  })}
                />

                <span className="red-text">{errors.password}</span>
              </div>
              <div className="input-field col-10 mx-auto">
                <label htmlFor="password2">Confirm Password</label>
                <br />
                <input
                  onChange={this.onChange}
                  value={this.state.password2}
                  error={errors.password2}
                  id="password2"
                  type="password"
                  className={classnames("col-12", {
                    invalid: errors.password2
                  })}
                />

                <span className="red-text">{errors.password2}</span>
              </div>
              <div className="col-12 text-center">
                <button
                  style={{
                    width: "150px",
                    borderRadius: "3px",
                    letterSpacing: "1.5px",
                    marginTop: "1rem"
                  }}
                  type="submit"
                  className="btn btn-primary waves-effect waves-light hoverable blue accent-3"
                >
                  Sign up
                </button>
              </div>
              <div className="col-10 mx-auto mt-4">
                <p className="grey-text text-darken-1" style={{fontSize:"0.75rem"}}>
                  Don't have an account? <Link to="/register">Register</Link>
                </p>
              </div>
            </form>
          </div>
        </div>
      </div>
      </>
    );
  }
}

Register.propTypes = {
  registerUser: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  auth: state.auth,
  errors: state.errors
});

export default connect(
  mapStateToProps,
  { registerUser }
)(withRouter(Register));
